import {
  addGift,
  deleteGift,
  editGift,
  getGift,
  operateCoupon,
} from "../../api/memberAPI";

/*
 * @Author: your name
 * @Date: 1970-01-01 08:00:00
 * @LastEditTime: 2021-01-21 13:00:13
 * @LastEditors: Please set LastEditors
 * @Description: In User Settings Edit
 * @FilePath: /webAdmin/src/store/modules/member.js
 */
const state = () => {
  return {
    giftList: [],
    couponList: [],
  };
};
const mutations = {
  GET_GIFT(state, gift) {
    state.giftList = gift;
  },
  ADD_GIFT(state, gift) {
    state.giftList.push(gift);
  },
  DELETE_GIFT(state, gift) {
    state.giftList.map((item, index) => {
      if (item.id === gift[0].id) {
        state.giftList.splice(index, 1);
      }
    });
  },
  EDIT_GIFT(state, gift) {
    state.giftList.map((item, index) => {
      if (item.id === gift.id) {
        state.giftList.splice(index, 1, gift);
      }
    });
  },
  GET_COUPON(state, coupon) {
    state.couponList = coupon;
  },
  ADD_COUPON(state, coupon) {
    state.couponList.push(coupon);
  },
  DELETE_COUPON(state, coupon) {
    state.couponList.map((item, index) => {
      if (item.id === coupon.id) {
        state.couponList.splice(index, 1);
      }
    });
  },
  SET_COUPON_LOADING(state, index) {
    state.couponList[index].loading = true;
  },
  SET_GIFT_DELETE_LOADING(state, data) {
    state.giftList[data.index].deleteLoading = data.status;
  },
};

const actions = {
  // 获取店铺积分兑换礼品
  async getShopGift({ commit }, shopInfo) {
    let ret = await getGift(shopInfo);
    let giftList = ret.data[1];
    let newGiftList = giftList.map((item) => {
      // item.editLoading = false;
      item.deleteLoading = false;
      return item;
    });
    commit("GET_GIFT", newGiftList);
    return ret.data;
  },
  // 添加礼品
  async addShopGift({ commit }, giftInfo) {
    let ret = await addGift(giftInfo);
    // ret.editLoading = false;
    ret.deleteLoading = false;
    commit("ADD_GIFT", ret.data);
  },
  // 修改礼品
  async editShopGift({ commit }, giftInfo) {
    let ret = await editGift(giftInfo);
    ret.deleteLoading = false;
    commit("EDIT_GIFT", ret.data);
  },
  // 删除礼品
  async deleteShopGift({ commit }, scope) {
    let btnLoading = {
      index: scope.$index,
      status: true,
    };
    commit("SET_GIFT_DELETE_LOADING", btnLoading);
    let submitObj = {
      gift_id: scope.row.id,
    };
    let ret = await deleteGift(submitObj);
    commit("DELETE_GIFT", ret.data);
  },
  //  获取优惠券
  async getShopCoupon({ commit }, shopInfo) {
    let ret = await operateCoupon(shopInfo);
    let couponList = ret.data[1];
    let newCouponList = couponList.map((item) => {
      item.loading = false;
      return item;
    });
    commit("GET_COUPON", newCouponList);
    return ret.data;
  },
  // 添加优惠券
  async addCoupon({ commit }, couponInfo) {
    let ret = await operateCoupon(couponInfo);
    ret.data.loading = false;
    commit("ADD_COUPON", ret.data);
  },
  // 删除优惠券
  async deleteCoupon({ commit }, scope) {
    let obj = {
      operate_mode: "delete",
      coupon_id: scope.row.id,
    };
    commit("SET_COUPON_LOADING", scope.$index);
    let ret = await operateCoupon(obj);
    if (ret.status === 200) {
      commit("DELETE_COUPON", ret.data);
    }
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
