import { $axios } from "./http";
import { parseObjectToGetUrl } from "@/util";
// 未读提现信息
export function unreadedWithdrawalInfo(params) {
  const url = "get_withdrawal_info";
  let obj = {
    operate_mode: "unread",
    ...params,
  };
  const ret = parseObjectToGetUrl(obj);
  return $axios.get(url + ret);
}

// 提款
export function withdrawal(params) {
  const url = "withdrawal";
  return $axios.post(url, params);
}

// 已读提现信息
export function readedWithdrawalInfo(params) {
  const url = "get_withdrawal_info";
  let obj = {
    operate_mode: "read",
    ...params,
  };
  const ret = parseObjectToGetUrl(obj);
  return $axios.get(url + ret);
}
// 查看未读信息
export function readWithdrawalInfo(params) {
  const url = "read_withdrawal_info";
  const ret = parseObjectToGetUrl(params);
  return $axios.get(url + ret);
}

// 查看所有待处理提现信息
export function getAllUnhandleInfo() {
  const url = "search_withdrawal_info";
  let obj = {
    operate_mode: "todo",
  };
  const ret = parseObjectToGetUrl(obj);
  return $axios.get(url + ret);
}

// 处理提现信息
export function handleWithdrawal(params) {
  const url = "success_withdrawal";
  return $axios.post(url, params);
}

// 查看历史提现信息
export function withdrawalData(params) {
  const url = "statistics_withdrawal";
  const ret = parseObjectToGetUrl(params);
  return $axios.get(url + ret);
}
