import { commodityAPI } from "@/api";
import { alterSortOrder } from "../../api/commodityAPI";
const state = () => {
  return {
    commodityCategorityList: [],
    commodityInfo: [],
    commodityList: [],
    commodityNum: null,
  };
};

const mutations = {
  SET_COMMODITY_CATEGORITY_LIST(state, payload) {
    state.commodityCategorityList = payload;
  },
  SET_COMMODITYINFO(state, payload) {
    state.commodityInfo = payload;
  },
  SET_COMMODITYLIST(state, payload) {
    state.commodityList = payload;
  },
  SET_COMMODITY_NUM(state, payload) {
    state.commodityNum = payload;
  },
  ADD_COMMODITY_CATEGORITY_LIST(state, payload) {
    state.commodityCategorityList.splice(
      state.commodityCategorityList.length - 1,
      0,
      payload
    );
  },
  DELETE_COMMODITY_CATEGORY_LIST(state, payload) {
    let obj = state.commodityCategorityList.filter(
      (item) => item.id === payload.id
    );
    state.commodityCategorityList.splice(
      state.commodityCategorityList.indexOf(...obj),
      1
    );
  },
  CHENGE_COMMODITY_CATEGORY_SORT(state, payload) {
    // 不是第一个
    if (payload > 0) {
      let deleteItem = state.commodityCategorityList.splice(payload, 1);
      state.commodityCategorityList.splice(payload - 1, 0, ...deleteItem);
    }
  },
  // 删除以后更新commoditylist
  UPDATE_COMMODITY_LIST(state, payload) {
    let obj = state.commodityList.filter((item) => item.id === payload);
    state.commodityList.splice(state.commodityList.indexOf(...obj), 1);
  },
};
const actions = {
  async getCommodityCategorityList({ commit }, payload) {
    let ret = await commodityAPI.getProductCategories(payload);
    commit("SET_COMMODITY_CATEGORITY_LIST", ret.data);
  },
  async getProductInfo({ commit }, payload) {
    let obj = {
      product_id: payload,
    };
    let ret = await commodityAPI.getProductInfo(obj);
    let data = {
      ...ret.data,
      size: JSON.parse(ret.data.size),
    };
    commit("SET_COMMODITYINFO", data);
    return data;
  },
  async modifyCommodity({ commit }, payload) {
    let ret = await commodityAPI.modifyCommodity(payload);
    // let commodityId = ret.data.id;
    // console.log('commodityId', commodityId);
    commit("SET_COMMODITYINFO", ret);
  },
  async getCommodityList({ commit }, payload) {
    let ret = await commodityAPI.getProductList(payload);
    commit("SET_COMMODITY_NUM", ret.data[0]);
    commit("SET_COMMODITYLIST", ret.data[1]);
    return ret.data;
  },
  // 添加商品
  async addCommodity(_, payload) {
    let ret = await commodityAPI.addCommodity(payload);
    return ret;
  },
  // 添加商品类别
  async addCommoditySort({ commit }, payload) {
    let ret = await commodityAPI.addCommoditySort(payload);
    if (ret.status === 200) {
      // let obj = { ...ret.data.data}
      commit("ADD_COMMODITY_CATEGORITY_LIST", ...ret.data.data);
    }
  },
  // 删除商品类别
  async deleteCommoditySort({ commit }, payload) {
    let ret = await commodityAPI.deleteCommoditySort(payload);
    if (ret.status === 200) {
      commit("DELETE_COMMODITY_CATEGORY_LIST", ret.data);
    }
  },
  // 修改类别排序
  async alterSortDisplayOrder({ commit }, payload) {
    let ret = await alterSortOrder(payload);
    if (ret.status === 200) {
      commit("SET_COMMODITY_CATEGORITY_LIST", ret.data);
    }
  },
  // 添加套餐
  async addSetMeal(_, payload) {
    let ret = await commodityAPI.addSetMeal(payload);
    return ret.data;
  },
  // 获取套餐列表
  async getSetMealList(_, payload) {
    let ret = await commodityAPI.getSetMealList(payload);
    return ret.data;
  },
  // 向指定套餐添加商品
  async addBuffetProduct(_, payload) {
    let ret = await commodityAPI.addBuffetProduct(payload);
    return ret.data;
  },
  // 删除指定套餐
  async delBuffet(_, payload) {
    let ret = await commodityAPI.delBuffet(payload);
    return ret.data;
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
