  border-right: solid 1px #e6e6e6;
  min-height: 100%;
  max-height: 100vh;
  .el-scrollbar {
    height: 100%;
  }
}
::v-deep .el-scrollbar__view {
  min-height: calc(100% - 64px);
}
.scrollbar_wrapper {
  height: 100%;
}

.btn_wrapper {
  position: absolute;
  bottom: 3em;
}
.logoutBtn {
  margin-left: 75px;
  margin-top: 3em;
  margin-bottom: 2em;
}
@media screen and(max-height: 650px) {
  .btn_wrapper {
    position: static;
    top: 370px;
  }
}
</style>
