import { $axios1 } from "./http";
import { parseObjectToGetUrl } from "@/util";

// excel数据
export function getExcelData(params) {
  const url = "get_orders/v1";
  const ret = parseObjectToGetUrl(params);
  return $axios1.get(url + ret);
}

// 年月收入排行
export function getIncomeOrder(params) {
  const url = "statistics_income_order/v1";
  const ret = parseObjectToGetUrl(params);
  return $axios1.get(url + ret);
}

// 获取分析数据
export function getAnalyzeInfo() {
  const url = "get_analyze_data/v1";
  return $axios1.get(url);
}

// 具体商品销售统计
export function getProductStatistics(params) {
  const url = "stastistics_product/v1";
  return $axios1.post(url, params);
}
