import { parseObjectToGetUrl } from "../util";
import { $axios1 } from "./http";

// 添加积分兑换礼品
export function addGift(params) {
  const url = "add_gift/v1";
  return $axios1.post(url, params);
}

// 删除积分礼品
export function deleteGift(params) {
  const url = "delete_shop_gift/v1";
  return $axios1.post(url, params);
}

// 修改积分礼品
export function editGift(params) {
  const url = "alter_shop_gift/v1";
  return $axios1.post(url, params);
}

// 获取店铺礼品列表
export function getGift(params) {
  const url = "get_shop_gifts/v1";
  let obj = parseObjectToGetUrl(params);
  return $axios1.get(url + obj);
}

// 操作优惠券
export function operateCoupon(params) {
  const url = "operate_shop_coupon/v1";
  return $axios1.post(url, params);
}
