// 添加满减信息
export function addReductionInfo(params) {
  const url = "operate_reduction";
  params = {
    operate_mode: "insert",
    ...params,
  };
  return $axios.post(url, params);
}

// 获取满减信息
export function getReductionInfo(params) {
  const url = "get_reduction_info";
  const ret = parseObjectToGetUrl(params);
  return $axios.get(url + ret);
}

// 修改满减信息
export function alterReductionInfo(params) {
  const url = "operate_reduction";
  params = {
    operate_mode: "alter",
    ...params,
  };
  return $axios.post(url, params);
}

// 删除满减信息
export function deleteReduction(params) {
  const url = "operate_reduction";
  params = {
    operate_mode: "delete",
    ...params,
  };
  return $axios.post(url, params);
}

