import { $axios1 } from "./http";
import { parseObjectToGetUrl } from "@/util";

export function getProductCategories(params) {
  const url = "get_product_categories/v1";
  let ret = parseObjectToGetUrl(params);
  return $axios1.get(url + ret);
}

export function getProductInfo(params) {
  const url = "get_product_info/v1";
  let ret = parseObjectToGetUrl(params);
  return $axios1.get(url + ret);
}

// 获取商品列表
export function getCommodityList(params) {
  const url = "get_commodity_list/v1";
  let ret = parseObjectToGetUrl(params);
  return $axios1.get(url + ret);
}

// 修改商品
export function modifyCommodity(params) {
  const url = "operate_product/v1";
  const mode = {
    operate_mode: "alter",
  };
  params = {
    ...mode,
    product_data: {
      ...params,
    },
  };
  return $axios1.post(url, params);
}

// 添加商品
export function addCommodity(params) {
  const url = "operate_product/v1";
  const mode = {
    operate_mode: "insert",
  };
  params = {
    product_data: {
      ...params,
    },
    ...mode,
  };
  return $axios1.post(url, params);
}

// 删除商品
export function deleteCommodity(params) {
  const url = "operate_product/v1";
  const mode = {
    operate_mode: "delete",
  };
  params = {
    ...params,
    ...mode,
  };
  return $axios1.post(url, params);
}

// 类别display_order修改
export function alterSortOrder(params) {
  const url = "operate_product_categories/v1";
  const obj = {
    operate_mode: "moveUp",
    category_info: params,
  };
  return $axios1.post(url, obj);
}

