import { userAPI, storeAPI } from "@/api";
import router from "@/router";
import { getShopInfo } from "../../api/storeAPI";
import { alterRam } from "../../api/userAPI";
import {
  clearLocal,
  clearSession,
  setLoginLocal,
  setLoginSession,
} from "../../util";
// import i18n from "@/lang/index.js";
const state = () => {
  return {
    refreshToken: "",
    accessToken: "",
    subAccount: [],
    loginData: {},
    tokenType: "",
  };
};

const mutations = {
  SET_REFRESH_TOKEN(state, refreshToken) {
    state.refreshToken = refreshToken;
  },
  SET_ACCESS_TOKEN(state, accessToken) {
    state.accessToken = accessToken;
  },
  SET_TOKEN_TYPE(state, tokenType) {
    state.tokenType = tokenType;
  },
  SET_SUB_ACCOUNT(state, preload) {
    state.subAccount = preload;
  },
  ADD_SUB_ACCOUNT(state, preload) {
    state.subAccount.push(preload);
  },
  DELETE_SUB_ACCOUNT(state, preload) {
    let obj = state.subAccount.filter((item) => item.id === preload);
    state.subAccount.splice(state.subAccount.indexOf(...obj), 1);
  },
  ALTER_SUB_ACCOUNT(state, payload) {
    let { id } = payload;
    let item = state.subAccount.filter((item) => item.id === id);
    let index = state.subAccount.indexOf(item);
    state.subAccount.splice(index, 1, payload);
  },
  SET_LOGIN_DATA(state, payload) {
    state.loginData = { ...state.loginData, ...payload };
  },
  CLEAR_LOGIN_DATA(state) {
    state.loginData = {};
  },
  CHANGE_BALANCE(state, payload) {
    state.loginData.balance -= payload;
  },
};

const actions = {
  // login
  async login({ commit }, userInfo) {
    function commitData(ret) {
      commit("SET_REFRESH_TOKEN ", ret.refresh_token);
      commit("SET_ACCESS_TOKEN", ret.access_token);
      commit("SET_TOKEN_TYPE", ret.token_type);
      commit("SET_LOGIN_DATA", ret.data);
    }

    let ret = await userAPI.login({
      email: userInfo.email,
      password: userInfo.password,
      grant_type: userInfo.grant_type,
    });
    if (ret.response) {
      // 错误
      return ret;
    } else {
      commitData(ret.data);
      setLoginSession(ret.data);
      // 如果点击了记住登录状态则将token存放在localStorage中
      userInfo.status && setLoginLocal(ret.data);
      router.push({ path: "/com" });
    }
  },

  // async authLogin({ commit }) {
  //   function commitData(ret) {
  //     commit("SET_REFRESH_TOKEN ", ret.refresh_token);
  //     commit("SET_ACCESS_TOKEN", ret.access_token);
  //     commit("SET_TOKEN_TYPE", ret.token_type);
  //     commit("SET_LOGIN_DATA", ret.data);
  //   }
  //   let obj = {
  //     refresh_token: sessionStorage.getItem("refreshToken"),
  //     grant_type: "refresh_token",
  //   };
  //   let ret = await userAPI.login(obj);

  //   if (ret.data.data) {
  //     setLoginSession(ret.data);
  //     commitData(ret.data);
  //   }
  // },

  // logout
  async logout({ commit }) {
    clearSession();
    clearLocal();
    commit("CLEAR_LOGIN_DATA");
    setTimeout(() => {
      router.push("/");
    }, 0);
  },
  // 商家子账号获取
  async getSubAccount({ commit }, preload) {
    let ret = await userAPI.getSubStoreList(preload);
    commit("SET_SUB_ACCOUNT", ret.data.list);
  },
  // 商家子账号注册
  async registerSubAccount({ commit }, preload) {
    let ret = await userAPI.ramCreate(preload);
    commit("ADD_SUB_ACCOUNT", ret.data);
  },
  // 商家子账户删除
  async deleteSubAccount({ commit }, payload) {
    await userAPI.deleteSubaccount(payload);
    commit("DELETE_SUB_ACCOUNT");
  },

  // 商家子账户修改
  async alterSubAccount(_, payload) {
    await alterRam(payload);
  },

  // 修改店铺营业状态
  async setBusinessStatus({ commit }, payload) {
    await storeAPI.setBusinessStatus(payload);
    commit();
  },

  // 修改商家基本信息
  async alterBusiness({ commit }, payload) {
    let ret = await storeAPI.alterShopInfo(payload);
    if (ret.status === 200) {
      commit("SET_LOGIN_DATA", payload);
    }
  },

  // 获取商家基本信息
  async getShopBasicInfo({ commit }, payload) {
    let ret = await getShopInfo(payload);
    if (ret.status === 200) {
      commit("SET_LOGIN_DATA", ret.data);
    }
  },

  // 修改商家图片
  async setStorePic(_, payload) {
    await storeAPI.setStorePic(payload);
  },
};
export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
