// 悉尼
const $axios = axios.create({
  timeout: 10000,
  baseURL:
    "https://qauovjfr97.execute-api.ap-southeast-2.amazonaws.com/default/",
});

$axios.interceptors.request.use(
  (config) => {
    config.headers.Authorization =
      (sessionStorage.getItem("tokenType") ||
        localStorage.getItem("tokenType") ||
        "") +
      " " +
      (sessionStorage.getItem("accessToken") ||
        localStorage.getItem("accessToken") ||
        "");
    return config;
  },
  (err) => {
    return Promise.reject(err);
  }
);

$axios1.interceptors.request.use(
  (config) => {
    config.headers.Authorization =
      (sessionStorage.getItem("tokenType") ||
        localStorage.getItem("tokenType") ||
        "") +
      " " +
      (sessionStorage.getItem("accessToken") ||
        localStorage.getItem("accessToken") ||
        "");
    return config;
  },
  (err) => {
    return Promise.reject(err);
  }
);

$axios1.interceptors.response.use(
  (response) => {
    return response;
  },
  (err) => {
    const { code } = err.response.data;
    switch (code) {
      // 令牌缺失
      case 1001:
        Vue.prototype.$message.error(i18n.t("error.authErr"));
        break;
      // 令牌格式不正确
      case 1002:
        break;
      // 令牌用户不存在
      case 1003:
        break;
      // token过期
      case 1004:
        clearLocal();
        clearSession();
        Vue.prototype.$message.error(i18n.t("error.tokenErr"));
        break;
      // 参数有误
      case 2006:
        break;
      // 店铺不存在
      case 2004:
        break;
      default:
        Vue.prototype.$message.error(i18n.t("error.serverErr"));
        break;
    }
    return err;
  }
);

$axios.interceptors.response.use(
  (response) => {
    return response;
  },
  (err) => {
    return Promise.reject(err.response);
  }
);

export function fetchGet(url, params) {
  return new Promise((resolve, reject) => {
    let newParams = params && parseObjectToGetUrl(params);
    $axios
      .get(url, newParams || "")
      .then(
        (response) => resolve(response),
        (err) => reject(err)
      )
      .catch((err) => reject(err));
  });
}

export function fetchPost(url, params) {
  return new Promise((resolve, reject) => {
    $axios
      .post(url, params)
      .then(
        (response) => resolve(response),
        (err) => reject(err)
      )
      .catch((err) => reject(err));
  });
}

export { $axios, $axios1, $axios2 };
